import React from 'react';
import './App.scss';

const goodsFromServer = [
  'Dumplings',
  'Carrot',
  'Eggs',
  'Ice cream',
  'Apple',
  'Bread',
  'Fish',
  'Honey',
  'Jam',
  'Garlic',
];

class App extends React.Component {
  state = {
    selectedGoods: [],
  }

  clearList = () => {
    this.setState({ selectedGoods: [] });
  }

  selectCancelGood = (item) => {
    this.setState((state) => {
      if (!state.selectedGoods.includes(item)) {
        return {
          selectedGoods: [...state.selectedGoods, item],
        };
      }

      const itemIndex = state.selectedGoods.indexOf(item);

      state.selectedGoods.splice(itemIndex, 1);

      return {
        selectedGoods: state.selectedGoods,
      };
    });
  }

  render() {
    const { selectedGoods } = this.state;

    return (
      <div className="App">
        <h1>
          {`Selected good: ${selectedGoods.join(', ')}`}
        </h1>
        <button
          type="button"
          onClick={this.clearList}
        >
          Clear All
        </button>
        {goodsFromServer.length}
        <ul>
          {goodsFromServer.map(item => (
            <li
              key={item}
              className={selectedGoods.indexOf(item) !== -1
                ? 'ready'
                : 'selected'}
            >
              {item}
              <button
                type="button"
                onClick={() => {
                  this.selectCancelGood(item);
                }}
              >
                {selectedGoods.indexOf(item) === -1 ? 'Select' : 'Remove'}
              </button>
            </li>
          ))}
        </ul>
      </div>
    );
  }
}

export default App;
